@import "https://fonts.googleapis.com/css?family=Roboto:500&display=swap";
body {
  --font-blue: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.navigation {
  margin: 0;
  padding: 0;
  display: flex;
  bottom: 0;
  left: 0;
}

.nav__logo {
  width: 40px;
  height: 40px;
}

.nav__item {
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--font-blue);
}

.is-active {
  color: var(--font-blue);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--font-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
